import React, { Fragment } from "react";
import { connect } from "react-redux";
import {
  Auth,
  FC_GetProfileDetails,
  FC_DeleteDocument,
  FC_Access,
} from "../../actions";
import { StoreState } from "../../reducers";
import {
  DocumentSubCategories,
  DocumentsCategories,
  DocumentsCategoriesWithCount,
  DocumentUser,
} from "../../interfaces";

import Profile from "./Profile";
import DocumentDetails from "./DocumentDetails";
import { AddUserDocument } from "../../components/AddUserDocument/AddUserDocument";
import { EditProfileDetails } from "./EditProfileDetails";
import { EditUserDocument } from "../../components/EditUserDocument/EditUserDocument";
import { ChangePassword } from "../../components/ChangePassword/ChangePassword";
import DocumentsList from "./DocumensList";
import {
  MainItem,
  // Search,
  SideNavigation,
  SubItem,
  SubItemContainer,
} from "../../components/Navigation/SideNavigation";
import { MdLabel, MdPrint } from "react-icons/md";
import { UserAccessList } from "../../utils/auth_access";

// props for the component
interface AppProps {
  auth: Auth;
  FC_GetProfileDetails: Function;
  FC_DeleteDocument: Function;
}

interface AppState {
  loading: boolean;
  selected_category: DocumentsCategories | null;
  selected_sub_category: DocumentSubCategories | null;
  selected_doc: DocumentUser | null;
  edit_doc: DocumentUser | null;
  create_new_doc: boolean;
  error: {
    target: string;
    msg: string;
  };
  updateProfile: boolean;
  changePassword: boolean;
  side_nav: boolean;
}

class _ProfileDetails extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      selected_category: null,
      selected_sub_category: null,
      selected_doc: null,
      edit_doc: null,
      create_new_doc: false,
      error: {
        target: "",
        msg: "",
      },
      updateProfile: false,
      changePassword: false,
      side_nav: false,
    };
  }

  componentDidMount = () => {
    this.setState({
      loading: true,
    });
    this.props.FC_GetProfileDetails(
      this.props.auth.user.user_id,
      (status: boolean, msg: string) => {
        if (status === true) {
          this.setState({
            loading: false,
          });
        } else {
          this.setState({
            loading: false,
            error: {
              msg: msg,
              target: "all",
            },
          });
        }
      }
    );

    console.log({
      User_access: FC_Access(this.props.auth, UserAccessList.do_exam),
    });
  };

  categoryHandler = (value: DocumentsCategories | null) =>
    this.setState({
      selected_category: value,
      selected_sub_category: null,
    });

  subCategoryHandler = (value: DocumentSubCategories) =>
    this.setState({
      selected_sub_category: value,
    });

  selectDocHandler = (value: DocumentUser | null) => {
    this.setState({
      selected_doc: value,
    });
  };

  editDocHandler = (value: DocumentUser | null) => {
    this.setState({
      edit_doc: value,
    });
  };

  addNewDocHandler = () =>
    this.setState((prevState: AppState) => ({
      create_new_doc: !prevState.create_new_doc,
    }));

  deleteDocHandler = (id: string) => {
    if (window.confirm("Are you sure you want to delete?") === true) {
      this.props.FC_DeleteDocument(
        { doc_id: id, user_id: this.props.auth.user.user_id },
        (status: boolean, msg: string) => {
          if (status === true) {
            console.log("Success");
          } else {
            this.setState({
              error: {
                target: "all",
                msg: "",
              },
            });
          }
        }
      );
    }
  };

  updateDocHandler = (doc: DocumentUser | null) => {
    console.log({ update_doc: doc });
    this.setState({
      edit_doc: doc,
      selected_doc: null,
    });
  };

  changePasswordHadler = () =>
    this.setState((prevState: AppState) => ({
      changePassword: !prevState.changePassword,
    }));

  updateProfileHandler = () =>
    this.setState((prevState: AppState) => ({
      updateProfile: !prevState.updateProfile,
    }));

  sideNavHandler = () =>
    this.setState((prevState: AppState) => ({
      side_nav: !prevState.side_nav,
    }));

  render() {
    const {
      user,
      document_types: { categories, sub_categories },
      documents,
    } = this.props.auth;
    let category_with_counts: DocumentsCategoriesWithCount[] = categories.map(
      (cat: DocumentsCategories) => {
        return {
          ...cat,
          count: documents.filter(
            (doc) => doc.category_id === cat.user_detail_category_id
          ).length,
        } as DocumentsCategoriesWithCount;
      }
    );

    if (this.state.loading === true) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading profile...
        </div>
      );
    }
    return (
      <Fragment>
        <SideNavigation>
          {/* <Search data={[1, 2, 3]} /> */}

          <section onClick={() => this.categoryHandler(null)} className="mt-4">
            <MainItem
              title={"Profile"}
              count={0}
              active={this.state.selected_category === null ? true : false}
            />
          </section>
          <div className="pt-1">
            {category_with_counts.map((itm, i: number) => (
              <Fragment key={itm.title}>
                <section onClick={() => this.categoryHandler(itm)}>
                  <MainItem
                    title={itm.title}
                    count={itm.count}
                    active={
                      this.state.selected_category?.user_detail_category_id ===
                      itm.user_detail_category_id
                        ? true
                        : false
                    }
                  />
                </section>

                {this.state.selected_category?.user_detail_category_id ===
                  itm.user_detail_category_id &&
                  sub_categories.filter(
                    (el) => el.category_id === itm.user_detail_category_id
                  ).length >= 1 && (
                    <SubItemContainer>
                      {sub_categories
                        .filter(
                          (el) => el.category_id === itm.user_detail_category_id
                        )
                        .map((sub_itm) => (
                          <section
                            key={sub_itm.user_detail_subcategory_id}
                            onClick={() => this.subCategoryHandler(sub_itm)}
                          >
                            <SubItem
                              title={sub_itm.title}
                              icon={MdLabel}
                              active={
                                this.state.selected_sub_category
                                  ?.user_detail_subcategory_id ===
                                sub_itm.user_detail_subcategory_id
                                  ? true
                                  : false
                              }
                            />
                          </section>
                        ))}
                    </SubItemContainer>
                  )}
              </Fragment>
            ))}
          </div>

          <button className="btn bg-blue-200 text-blue-600 text-center mt-2 w-full">
            <MdPrint className="inline-block mr-1" /> Print CV
          </button>
        </SideNavigation>

        <AddUserDocument
          active={this.state.create_new_doc}
          close={this.addNewDocHandler}
          category={this.state.selected_category}
          sub_category={this.state.selected_sub_category}
        />

        {this.state.edit_doc !== null && (
          <EditUserDocument
            categories={this.props.auth.document_types.categories}
            document={this.state.edit_doc}
            close={() => this.setState({ edit_doc: null })}
          />
        )}

        {this.state.selected_doc !== null && (
          <Fragment>
            <DocumentDetails
              category={this.state.selected_category}
              sub_category={this.state.selected_sub_category}
              doc={this.state.selected_doc}
              categories={categories}
              sub_categories={sub_categories}
              close={() => this.selectDocHandler(null)}
              Update={this.updateDocHandler}
              Delete={this.deleteDocHandler}
            />
          </Fragment>
        )}

        {/* // edit progile */}
        {this.state.updateProfile === true && (
          <EditProfileDetails close={this.updateProfileHandler} />
        )}

        {/* // edit progile */}
        {this.state.changePassword === true && (
          <ChangePassword close={this.changePasswordHadler} />
        )}

        <div className="">
          <div className="mt-3  pt-2">
            <div className="shadow rounded bg-white bg-opacity-50">
              {this.state.selected_category === null ? (
                <Profile
                  user={user}
                  changePassword={this.changePasswordHadler}
                  editProfile={this.updateProfileHandler}
                />
              ) : (
                <DocumentsList
                  documents={documents}
                  selected_category={this.state.selected_category}
                  selected_sub_category={this.state.selected_sub_category}
                  selectDoc={this.selectDocHandler}
                  addNewDoc={this.addNewDocHandler}
                  categories={categories}
                  sub_categories={sub_categories}
                />
              )}
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const ProfileDetails = connect(mapStateToProps, {
  FC_GetProfileDetails,
  FC_DeleteDocument,
})(_ProfileDetails);
