import React, { Fragment } from "react";
import { connect } from "react-redux";
import { Auth, FC_EditUserDocument } from "../../actions";
import { StoreState } from "../../reducers";
import {
  DocumentsCategories,
  DocumentUser,
  DocumentUser_API,
} from "../../interfaces";
import Alert, { AlertInterface } from "../../components/Alert/Alert";
import Modal, { ModalSize, Themes } from "../Modal/Modal";

// props for the component
interface AppProps {
  auth: Auth;
  categories: DocumentsCategories[];
  document: DocumentUser;
  close: Function;
  FC_EditUserDocument: Function;
}
enum ErrorType {
  title,
  company,
  description,
  email,
  end_year,
  files,
  names,
  phone_number,
  school,
  start_year,
  sub_category_id,
  main_error,
  main_success,
  none,
  year,
}
interface AppState {
  loading: boolean;
  document: DocumentUser;
  category: DocumentsCategories | null;
  error: {
    target: ErrorType;
    msg: string;
  };
  success: boolean;
  title: string;
  description: string;
  company: string;
  email: string;
  end_year: string;
  names: string;
  phone_number: string;
  school: string;
  start_year: string;
  year: string;
}

class _EditUserDocument extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      success: false,
      category: null,
      error: {
        target: ErrorType.none,
        msg: "",
      },
      document: this.props.document,
      title: "",
      description: "",
      email: "",
      end_year: "",
      names: "",
      phone_number: "",
      school: "",
      start_year: "",
      year: "",
      company: "",
    };
  }

  componentDidMount = () => {
    if (this.props.categories.length <= 0 || this.props.document === null) {
      this.setState({
        error: {
          target: ErrorType.none,
          msg: "The document selected does not have a category",
        },
      });
    } else {
      const category = this.props.categories.find(
        (itm) => itm.user_detail_category_id === this.props.document.category_id
      );
      if (category === undefined) {
        this.setState({
          error: {
            target: ErrorType.none,
            msg: "The document selected does not have a category",
          },
        });
      } else {
        this.setState({
          category: category,
          title: this.props.document.title,
          description: this.props.document.description,
          email: this.props.document.email,
          end_year: this.props.document.end_year,
          names: this.props.document.names,
          phone_number: this.props.document.phone_number,
          school: "",
          start_year: this.props.document.start_year,
          year: this.props.document.year,
          company: this.props.document.company,
        });
      }
    }
  };

  submitHandler = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    this.setState({
      error: {
        target: ErrorType.none,
        msg: "",
      },
    });
    let userDocument = {
      ...this.props.document,
      title: this.state.title,
      description: this.state.description,
      email: this.state.email,
      end_year: this.state.end_year,
      names: this.state.names,
      phone_number: this.state.phone_number,
      school: "",
      start_year: this.state.start_year,
      year: this.state.year,
      company: this.state.company,
      category_subcategory_data: "",
    } as DocumentUser_API;

    const fields_access =
      this.state.category?.fields === undefined
        ? []
        : this.state.category?.fields.map((name) => name.toLowerCase());

    // title
    if (fields_access.includes("title") === true) {
      if (this.state.title.length <= 0) {
        return this.setState({
          error: {
            target: ErrorType.title,
            msg: "Please add the title",
          },
        });
      }
    }

    // description
    if (fields_access.includes("description") === true) {
      if (this.state.description.length <= 0) {
        return this.setState({
          error: {
            target: ErrorType.description,
            msg: "Please add the description",
          },
        });
      }
    }

    // school
    if (fields_access.includes("school") === true) {
      if (this.state.school.length <= 0) {
        return this.setState({
          error: {
            target: ErrorType.school,
            msg: "Please add the school",
          },
        });
      }
    }

    // email
    if (fields_access.includes("email") === true) {
      if (this.state.email.length <= 0) {
        return this.setState({
          error: {
            target: ErrorType.email,
            msg: "Please add the email",
          },
        });
      }
    }

    // names
    if (fields_access.includes("names") === true) {
      if (this.state.names.length <= 0) {
        return this.setState({
          error: {
            target: ErrorType.names,
            msg: "Please add the names",
          },
        });
      }
    }

    // phone_number
    if (fields_access.includes("phone_number") === true) {
      if (this.state.phone_number.length <= 0) {
        return this.setState({
          error: {
            target: ErrorType.phone_number,
            msg: "Please add the phone_number",
          },
        });
      }
    }

    // company
    if (fields_access.includes("company") === true) {
      if (this.state.company.length <= 0) {
        return this.setState({
          error: {
            target: ErrorType.company,
            msg: "Please add the company",
          },
        });
      }
    }

    // year
    if (fields_access.includes("year") === true) {
      if (this.state.year.length <= 0) {
        return this.setState({
          error: {
            target: ErrorType.year,
            msg: "Please add the year",
          },
        });
      }
    }

    // start_year
    if (fields_access.includes("start_year") === true) {
      if (this.state.start_year.length <= 0) {
        return this.setState({
          error: {
            target: ErrorType.start_year,
            msg: "Please add the start_year",
          },
        });
      }
    }

    // end_year
    if (fields_access.includes("end_year") === true) {
      if (this.state.end_year.length <= 0) {
        return this.setState({
          error: {
            target: ErrorType.end_year,
            msg: "Please add the end_year",
          },
        });
      }
    }

    this.setState({
      loading: true,
    });
    // send the data
    this.props.FC_EditUserDocument(
      userDocument,
      this.props.auth.user.user_id,
      (status: boolean, msg: string) => {
        if (status === true) {
          this.setState({
            loading: false,
            success: true,
          });
        } else {
          this.setState({
            loading: false,
            error: {
              target: ErrorType.main_error,
              msg: msg,
            },
          });
        }
      }
    );
  };

  inputChangeHandler = (e: React.ChangeEvent<HTMLInputElement>): void => {
    this.setState({
      ...this.state,
      error: {
        target: ErrorType.none,
        msg: "",
      },
      [e.currentTarget.name]: e.target.value,
    } as Pick<AppState, keyof AppState>);
  };

  render() {
    let fields_access: string[] =
      this.state.category?.fields === undefined
        ? []
        : this.state.category?.fields.map((name) => name.toLowerCase());

    return (
      <Fragment>
        <Modal
          widthSizeClass={ModalSize.medium}
          backDrop={true}
          backDropClose={false}
          close={() => this.props.close()}
          displayClose={true}
          theme={Themes.default}
          title={
            <label className="text-xl m-0 text-bold text-gray-700">
              Update document
            </label>
          }
          padding={{ body: true, footer: true, title: true }}
        >
          <div className="p-2">
            {this.state.error.target === ErrorType.main_error && (
              <Alert
                theme={AlertInterface.danger}
                title={this.state.error.msg}
              />
            )}

            {this.state.error.target === ErrorType.main_success && (
              <Alert
                theme={AlertInterface.success}
                title={this.state.error.msg}
              />
            )}

            {this.state.success === true && (
              <Alert
                theme={AlertInterface.success}
                title={"Details updated successfully"}
              />
            )}

            <div className="mt-1">
              <div className="p-2">
                <label htmlFor="title" className="d-block">
                  Category
                </label>

                <div className="input-white w-20 inline-block border-gray-300 border-2 font-bold bg-blue-200">
                  {this.state.category?.title}
                </div>
              </div>
            </div>
            <form onSubmit={this.submitHandler}>
              <div className="bg-white">
                {fields_access.includes("title") === true && (
                  <div className="p-2">
                    <label htmlFor="title" className="d-block">
                      Title
                    </label>
                    <input
                      value={this.state.title}
                      type="text"
                      name="title"
                      onChange={this.inputChangeHandler}
                      className={`input-white w-20 inline-block  ${
                        this.state.error?.target === ErrorType.title
                          ? "border-red-500 border-2"
                          : "border-gray-500 border-2"
                      }`}
                      placeholder="Enter the title"
                      disabled={this.state.loading}
                    />
                    {this.state.error?.target === ErrorType.title && (
                      <span className="text-red-600">
                        {this.state.error?.msg}
                      </span>
                    )}
                  </div>
                )}

                {fields_access.includes("description") === true && (
                  <div className="p-2">
                    <label htmlFor="description" className="d-block">
                      Description
                    </label>
                    <input
                      value={this.state.description}
                      type="text"
                      name="description"
                      onChange={this.inputChangeHandler}
                      className={`input-white w-20 inline-block  ${
                        this.state.error?.target === ErrorType.description
                          ? "border-red-500 border-2"
                          : "border-gray-500 border-2"
                      }`}
                      placeholder="Enter the description"
                      disabled={this.state.loading}
                    />
                    {this.state.error?.target === ErrorType.description && (
                      <span className="text-red-600">
                        {this.state.error?.msg}
                      </span>
                    )}
                  </div>
                )}

                {fields_access.includes("school") === true && (
                  <div className="p-2">
                    <label htmlFor="school" className="d-block">
                      School
                    </label>
                    <input
                      value={this.state.school}
                      type="text"
                      name="school"
                      onChange={this.inputChangeHandler}
                      className={`input-white w-20 inline-block  ${
                        this.state.error?.target === ErrorType.school
                          ? "border-red-500 border-2"
                          : "border-gray-500 border-2"
                      }`}
                      placeholder="Your school"
                      disabled={this.state.loading}
                    />
                    {this.state.error?.target === ErrorType.school && (
                      <span className="text-red-600">
                        {this.state.error?.msg}
                      </span>
                    )}
                  </div>
                )}

                {fields_access.includes("email") === true && (
                  <div className="p-2">
                    <label htmlFor="email" className="d-block">
                      Email
                    </label>
                    <input
                      value={this.state.email}
                      type="text"
                      name="email"
                      onChange={this.inputChangeHandler}
                      className={`input-white w-20 inline-block  ${
                        this.state.error?.target === ErrorType.email
                          ? "border-red-500 border-2"
                          : "border-gray-500 border-2"
                      }`}
                      placeholder="Your email"
                      disabled={this.state.loading}
                    />
                    {this.state.error?.target === ErrorType.email && (
                      <span className="text-red-600">
                        {this.state.error?.msg}
                      </span>
                    )}
                  </div>
                )}

                {fields_access.includes("names") === true && (
                  <div className="p-2">
                    <label htmlFor="names" className="d-block">
                      Names
                    </label>
                    <input
                      value={this.state.names}
                      type="text"
                      name="names"
                      onChange={this.inputChangeHandler}
                      className={`input-white w-20 inline-block  ${
                        this.state.error?.target === ErrorType.names
                          ? "border-red-500 border-2"
                          : "border-gray-500 border-2"
                      }`}
                      placeholder="Your names"
                      disabled={this.state.loading}
                    />
                    {this.state.error?.target === ErrorType.names && (
                      <span className="text-red-600">
                        {this.state.error?.msg}
                      </span>
                    )}
                  </div>
                )}

                {fields_access.includes("phone_number") === true && (
                  <div className="p-2">
                    <label htmlFor="phone_number" className="d-block">
                      Phone number
                    </label>
                    <input
                      value={this.state.phone_number}
                      type="text"
                      name="phone_number"
                      onChange={this.inputChangeHandler}
                      className={`input-white w-20 inline-block  ${
                        this.state.error?.target === ErrorType.phone_number
                          ? "border-red-500 border-2"
                          : "border-gray-500 border-2"
                      }`}
                      placeholder="Your phone_number"
                      disabled={this.state.loading}
                    />
                    {this.state.error?.target === ErrorType.phone_number && (
                      <span className="text-red-600">
                        {this.state.error?.msg}
                      </span>
                    )}
                  </div>
                )}

                {fields_access.includes("company") === true && (
                  <div className="p-2">
                    <label htmlFor="company" className="d-block">
                      Company
                    </label>
                    <input
                      value={this.state.company}
                      type="text"
                      name="company"
                      onChange={this.inputChangeHandler}
                      className={`input-white w-20 inline-block  ${
                        this.state.error?.target === ErrorType.company
                          ? "border-red-500 border-2"
                          : "border-gray-500 border-2"
                      }`}
                      placeholder="Company name"
                      disabled={this.state.loading}
                    />
                    {this.state.error?.target === ErrorType.company && (
                      <span className="text-red-600">
                        {this.state.error?.msg}
                      </span>
                    )}
                  </div>
                )}

                {fields_access.includes("year") === true && (
                  <div className="p-2">
                    <label htmlFor="year" className="d-block">
                      Year
                    </label>
                    <input
                      value={this.state.year}
                      type="date"
                      name="year"
                      onChange={this.inputChangeHandler}
                      className={`input-white w-20 inline-block  ${
                        this.state.error?.target === ErrorType.year
                          ? "border-red-500 border-2"
                          : "border-gray-500 border-2"
                      }`}
                      placeholder="year"
                      disabled={this.state.loading}
                    />
                    {this.state.error?.target === ErrorType.year && (
                      <span className="text-red-600">
                        {this.state.error?.msg}
                      </span>
                    )}
                  </div>
                )}

                {fields_access.includes("start_year") === true && (
                  <div className="p-2">
                    <label htmlFor="start_year" className="d-block">
                      Start year
                    </label>
                    <input
                      value={this.state.start_year}
                      type="date"
                      name="start_year"
                      onChange={this.inputChangeHandler}
                      className={`input-white w-20 inline-block  ${
                        this.state.error?.target === ErrorType.start_year
                          ? "border-red-500 border-2"
                          : "border-gray-500 border-2"
                      }`}
                      placeholder="start_year"
                      disabled={this.state.loading}
                    />
                    {this.state.error?.target === ErrorType.start_year && (
                      <span className="text-red-600">
                        {this.state.error?.msg}
                      </span>
                    )}
                  </div>
                )}

                {fields_access.includes("end_year") === true && (
                  <div className="p-2">
                    <label htmlFor="end_year" className="d-block">
                      End year
                    </label>
                    <input
                      value={this.state.end_year}
                      type="date"
                      name="end_year"
                      onChange={this.inputChangeHandler}
                      className={`input-white w-20 inline-block  ${
                        this.state.error?.target === ErrorType.end_year
                          ? "border-red-500 border-2"
                          : "border-gray-500 border-2"
                      }`}
                      placeholder="end_year"
                      disabled={this.state.loading}
                    />
                    {this.state.error?.target === ErrorType.end_year && (
                      <span className="text-red-600">
                        {this.state.error?.msg}
                      </span>
                    )}
                  </div>
                )}

                <div className="p-2  text-right">
                  <button
                    className="py-2 font-bold  px-8 rounded bg-hero text-white inline-block"
                    disabled={this.state.loading}
                    type="submit"
                  >
                    {this.state.loading === true
                      ? "Loading..."
                      : "Update document"}
                  </button>
                </div>
              </div>
            </form>
          </div>
        </Modal>
      </Fragment>
    );
  }
}

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const EditUserDocument = connect(mapStateToProps, {
  FC_EditUserDocument,
})(_EditUserDocument);
