import React from "react";
import { MdCheckCircle, MdPanoramaFishEye } from "react-icons/md";
import { connect } from "react-redux";
import { Auth } from "../../actions";
import { StoreState } from "../../reducers";
import {
  FC_ChangePassword,
  FC_ChangePassword_Interface,
} from "../../actions/auth";
import {
  // validateEmail,
  hasUppercase,
  hasLowercase,
  hasNumber,
  hasSpecialcase,
} from "../../utils/validation";
import Modal, { ModalSize, Themes } from "../Modal/Modal";
// props for the component
interface AppProps {
  auth: Auth;
  close: Function;
  FC_ChangePassword: Function;
}

enum ErrorTarget {
  old_password,
  new_password,
  re_new_password,
  main__error,
  main_success,
  none,
}
interface AppState {
  loading: boolean;
  error: {
    target: ErrorTarget;
    msg: string;
  };
  old_password: string;
  new_password: string;
  re_new_password: string;
  password_validation: {
    uppercase: boolean;
    lowercase: boolean;
    number: boolean;
    length: boolean;
    special: boolean;
  };
}

class _ChangePassword extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      error: {
        target: ErrorTarget.none,
        msg: "",
      },
      old_password: "",
      new_password: "",
      re_new_password: "",
      password_validation: {
        uppercase: false,
        lowercase: false,
        number: false,
        length: false,
        special: false,
      },
    };
  }

  handleChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
    if (e.currentTarget.name === "new_password") {
      this.setState({
        ...this.state,
        error: {
          target: ErrorTarget.none,
          msg: "",
        },
        [e.currentTarget.name]: e.target.value,
        password_validation: {
          uppercase: hasUppercase(e.target.value),
          lowercase: hasLowercase(e.target.value),
          special: hasSpecialcase(e.target.value),
          number: hasNumber(e.target.value),
          length: e.target.value.length >= 8 && e.target.value.length < 64,
        },
      } as Pick<AppState, keyof AppState>);
    } else {
      this.setState({
        ...this.state,
        error: {
          target: ErrorTarget.none,
          msg: "",
        },
        [e.currentTarget.name]: e.target.value,
      } as Pick<AppState, keyof AppState>);
    }
  };

  updateAccountHandler = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    if (this.state.old_password.length <= 4) {
      return this.setState({
        error: {
          target: ErrorTarget.old_password,
          msg: "Please enter the current password",
        },
      });
    }

    if (
      !(
        this.state.password_validation.uppercase &&
        this.state.password_validation.lowercase &&
        this.state.password_validation.number &&
        this.state.password_validation.special &&
        this.state.password_validation.length
      )
    ) {
      return this.setState({
        error: {
          target: ErrorTarget.new_password,
          msg: "Password must pass the validation test",
        },
      });
    }

    // check for errors
    if (this.state.new_password !== this.state.re_new_password) {
      return this.setState({
        error: {
          target: ErrorTarget.re_new_password,
          msg: "Does not match to the new password",
        },
      });
    }

    this.setState({
      loading: true,
      error: {
        target: ErrorTarget.none,
        msg: "",
      },
    });

    this.props.FC_ChangePassword(
      {
        new_password: this.state.new_password,
        old_password: this.state.old_password,
        user_id: this.props.auth.user.user_id,
      } as FC_ChangePassword_Interface,
      (status: boolean, msg: string) => {
        if (status === true) {
          this.setState({
            loading: false,
            old_password: "",
            new_password: "",
            re_new_password: "",
            error: {
              target: ErrorTarget.main_success,
              msg: "Your password has been updated successfully",
            },
          });
        } else {
          this.setState({
            loading: false,
            error: {
              target: ErrorTarget.main__error,
              msg: msg,
            },
          });
        }
      }
    );
  };

  render() {
    let ValidationDesign = !(
      this.state.password_validation.uppercase &&
      this.state.password_validation.lowercase &&
      this.state.password_validation.number &&
      this.state.password_validation.special &&
      this.state.password_validation.length
    ) ? (
      <div className="px-2 mb-2 bg-white rounded shadow-lg mx-2 p-2 -mt-2">
        <div className="p-1 border-gray-200 border rounded-full flex">
          {this.state.password_validation.lowercase && (
            <div className="bg-blue-500 pt-1 rounded-l-full inline-block- w-1/6"></div>
          )}
          {this.state.password_validation.uppercase && (
            <div className="bg-blue-500 pt-1  inline-block- w-1/6"></div>
          )}
          {this.state.password_validation.number && (
            <div className="bg-blue-500 pt-1  inline-block- w-1/6"></div>
          )}
          {this.state.password_validation.length && (
            <div className="bg-blue-500 pt-1  inline-block- w-1/6"></div>
          )}
          {this.state.password_validation.special && (
            <div className="bg-blue-500 pt-1 rounded-r-full inline-block- w-2/6"></div>
          )}
        </div>

        <h5>Password must:</h5>
        <div className="pt-1 text-sm">
          <div
            className={`${
              this.state.password_validation.lowercase
                ? "text-green-600 font-bold"
                : "text-gray-500"
            }`}
          >
            {this.state.password_validation.lowercase ? (
              <MdCheckCircle className="inline-block" />
            ) : (
              <MdPanoramaFishEye className="inline-block" />
            )}{" "}
            <span>Include a lowercase character</span>
          </div>

          <div
            className={`${
              this.state.password_validation.uppercase
                ? "text-green-600 font-bold"
                : "text-gray-500"
            }`}
          >
            {this.state.password_validation.uppercase ? (
              <MdCheckCircle className="inline-block" />
            ) : (
              <MdPanoramaFishEye className="inline-block" />
            )}{" "}
            <span>Include an uppercase character</span>
          </div>

          <div
            className={`${
              this.state.password_validation.number
                ? "text-green-600 font-bold"
                : "text-gray-500"
            }`}
          >
            {this.state.password_validation.number ? (
              <MdCheckCircle className="inline-block" />
            ) : (
              <MdPanoramaFishEye className="inline-block" />
            )}{" "}
            <span>Include a Number</span>
          </div>

          <div
            className={`${
              this.state.password_validation.length
                ? "text-green-600 font-bold"
                : "text-gray-500"
            }`}
          >
            {this.state.password_validation.length ? (
              <MdCheckCircle className="inline-block" />
            ) : (
              <MdPanoramaFishEye className="inline-block" />
            )}{" "}
            <span>Between 8 and 64 character</span>
          </div>

          <div
            className={`${
              this.state.password_validation.special
                ? "text-green-600 font-bold"
                : "text-gray-500"
            }`}
          >
            {this.state.password_validation.special ? (
              <MdCheckCircle className="inline-block" />
            ) : (
              <MdPanoramaFishEye className="inline-block" />
            )}{" "}
            <span>A special character</span>
          </div>
        </div>
      </div>
    ) : null;

    return (
      <>
        <Modal
          widthSizeClass={ModalSize.medium}
          backDrop={true}
          backDropClose={false}
          close={() => this.props.close()}
          displayClose={true}
          theme={Themes.default}
          title={
            <label className="text-xl m-0 text-bold text-gray-700">
              Change password
            </label>
          }
          padding={{ body: true, footer: true, title: true }}
        >
          <div className="bg-white rounded-b-md">
            <form onSubmit={this.updateAccountHandler}>
              {this.state.error.target === ErrorTarget.main_success && (
                <div className="p-2 m-2 text-center rounded-md bg-green-200 text-green-700 animate__zoomIn animate__animated">
                  {this.state.error.msg}
                </div>
              )}

              {this.state.error.target === ErrorTarget.main__error && (
                <div className="p-2 m-2 text-center rounded-md bg-red-200 text-red-700 animate__zoomIn animate__animated">
                  {this.state.error.msg}
                </div>
              )}

              <div className="mt-2">
                <div className="p-2 ">
                  <label htmlFor="title" className="d-block">
                    Current password
                  </label>
                  <input
                    type="text"
                    name="old_password"
                    value={this.state.old_password}
                    className={`font-bold input-white w-20 inline-block  ${
                      this.state.error?.target === ErrorTarget.old_password
                        ? "border-red-500 border-2"
                        : "border-gray-500 border-2"
                    }`}
                    placeholder="Current password"
                    onChange={this.handleChange}
                    disabled={this.state.loading}
                    required
                  />
                  {this.state.error?.target === ErrorTarget.old_password && (
                    <span className="text-red-600">
                      {this.state.error?.msg}
                    </span>
                  )}
                </div>
              </div>
              <div className="mt-3">
                <div className="p-2 ">
                  <label
                    htmlFor="title"
                    className="d-block"
                    title="Re-enter the new password"
                  >
                    New Password
                  </label>
                  <input
                    type="text"
                    name="new_password"
                    value={this.state.new_password}
                    className={`font-bold input-white w-20 inline-block  ${
                      this.state.error?.target === ErrorTarget.new_password
                        ? "border-red-500 border-2"
                        : "border-gray-500 border-2"
                    }`}
                    placeholder="New password"
                    onChange={this.handleChange}
                    disabled={this.state.loading}
                    required
                  />
                  {this.state.error?.target === ErrorTarget.new_password && (
                    <span className="text-red-600">
                      {this.state.error?.msg}
                    </span>
                  )}
                </div>
              </div>
              {ValidationDesign}

              <div className="mt-2">
                <div className="p-2 ">
                  <label
                    htmlFor="title"
                    className="d-block"
                    title="Re-enter the new password"
                  >
                    Re-Password
                  </label>
                  <input
                    type="text"
                    name="re_new_password"
                    value={this.state.re_new_password}
                    className={`font-bold input-white w-20 inline-block  ${
                      this.state.error?.target === ErrorTarget.re_new_password
                        ? "border-red-500 border-2"
                        : "border-gray-500 border-2"
                    }`}
                    placeholder="Re-New password"
                    onChange={this.handleChange}
                    disabled={this.state.loading}
                    required
                  />
                  {this.state.error?.target === ErrorTarget.re_new_password && (
                    <span className="text-red-600">
                      {this.state.error?.msg}
                    </span>
                  )}
                </div>
              </div>

              <div className="p-2">
                <button
                  className="py-2 font-bold  px-8 rounded bg-hero text-white inline-block"
                  disabled={this.state.loading}
                  type="submit"
                >
                  {this.state.loading === true ? (
                    <b className="animate__animated animate__fadeIn animate__infinite">
                      Changing password...
                    </b>
                  ) : (
                    "Change password"
                  )}
                </button>
              </div>
            </form>
          </div>
        </Modal>

        {/* 
        <div
          className="animate__animated animate__fadeIn fixed top-0 right-0 left-0 bottom-0 z-40 bg-gray-900 bg-opacity-50"
          onClick={() => this.props.close()}
        ></div>

        <div className="animate__animated animate__zoomIn animate__faster fixed top-2 right-0 left-0  z-50  w-full  max-w-screen-md  max-w-screen-lg- h-auto m-auto">
          <div className="bg-gray-300 rounded-t-md p-4 text-center">
            <div className="text-right -mb-8">
              <MdCancel
                className="text-red-600 inline-block text-2xl"
                onClick={() => this.props.close()}
              />
            </div>
            <div className="text-center ">
              <div className="text-6xl">
                <MdLock className="inline-block text-gray-600" />
              </div>
              <h2>Change password</h2>{" "}
            </div>
          </div>
          
        </div> */}
      </>
    );
    // return (<div>hello</div>)
  }
}

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const ChangePassword = connect(mapStateToProps, {
  FC_ChangePassword,
})(_ChangePassword);
